.btn-control-round {
  width: 40px;
  height: 40px;
  border: none;
  padding: 0;
  border-radius: 50%;
  margin: 5px;
  background-color: #a8ff66;
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
  font-size: 0;
}
.btn-control-round:hover {
  background-color: #a8ff66 !important;
}

.btn-control-round-lg {
  width: 60px;
  height: 60px;
}

.btn-control-round-lg.btn-play {
  background-image: url("../../vendor/font-awesome/play-solid.svg");
  background-position: 17px 10px;
}

.btn-control-round.btn-stop {
  background-image: url("../../vendor/font-awesome/stop-solid.svg");
}

.btn-control-round.btn-pause {
  background-image: url("../../vendor/font-awesome/pause-solid.svg");
}

.btn-control-round-clear {
  background-image: url("../../vendor/font-awesome/trash-can-solid.svg");
}

.muski-sequencer-matrix th {
  font-size: 0.85rem;
  font-weight: normal;
}
.muski-sequencer-matrix td {
  width: 40px;
  height: 40px;
  border: 2px solid white;
  background-color: transparent;
}
.muski-sequencer-matrix .muski-sequencer-cell {
  display: block;
  width: 40px;
  height: 40px;
  background-color: #dcdcdc;
  border: none;
}
.muski-sequencer-matrix .muski-sequencer-cell.active {
  outline: 3px solid #ffff4b;
  background-color: #efef9c !important;
}
.muski-sequencer-matrix .muski-sequencer-cell.on {
  background: none;
  background-color: #61dcbd !important;
}
.muski-sequencer-matrix .muski-sequencer-cell.active.on {
  background: none;
  outline: 3px solid #ffff4b;
  background-color: #61dc7e !important;
}
.muski-sequencer-matrix .muski-sequencer-row-label {
  padding-right: 1em;
  text-align: right;
}
.muski-sequencer-matrix .muski-sequencer-col-label {
  text-align: center;
}

